import React from 'react';

const VRegister = (props) => {
  const [form_data, setFormData] = React.useState({login_id: '', password: '', email: ''});

  const handleInput = (e) => {
    let name = e.target.name;

    setFormData(state => {
      return {
        ...state,
        [name]: e.target.value
      };
    });
  };

  const submit = (e) => {
    console.log(form_data);
    e.preventDefault();
  }

  return (
    <form onSubmit={submit}>
      <div className="mx-auto w-100p" style={{width: `${100}%`, maxWidth: `${20}em`}}>
        <input type="text" name="login_id" value={form_data['login_id']} onChange={handleInput} placeholder="ID"/>        
        <input type="password" name="password" value={form_data['password']} onChange={handleInput} placeholder="PASSWORD"/>        
        <input type="email" name="email" value={form_data['email']} onChange={handleInput} placeholder="EMAIL"/>
        <div>
          <input type="submit" value="REGISTER"/>
        </div>
      </div>
    </form>
  );
}

 
export default VRegister;