import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import { VLogin, VMain, VRegister } from '../views';


const MainRoutes = (props) => {
  return ( 
    <Switch>
      <Route path="/auth/register" render={(props) => <VRegister {...props}/>}/>
      <Route path="/auth/login"    render={(props) => <VLogin {...props}/>}/>
      <Route exact path="/"        render={(props) => <VMain  {...props}/>}/>
      <Route                       render={(props) => <Redirect to="/" />}/>
    </Switch>
  );
}
 
export default MainRoutes;