import { AppState } from "../contexts";
import { ModalAlert } from "../modals";

const VMain = () => {
  return (
    <div>
      <div>
        Here's main
      </div>
      <br/>

      <AppState.Consumer>
        {state => (
          <div style={{ whiteSpace: 'pre-wrap' }}>
            <div>
              <button type='button' onClick={async e => {
                const modal_result = await state.pushMessageModal({
                  Modal: ModalAlert,
                  props: {
                    message: '테스트 메세지입니다.'
                  },
                  onConfirm: () => { console.log('CONFIRM'); },
                  onCancel: () => { console.log('CANCEL 73473'); }
                });
                console.log('MODAL_CONFIRMED? : ' + modal_result);
                if (modal_result === false)
                  return;
                await state.pushMessageModal({
                  Modal: ModalAlert,
                  props: {
                    message: '테스트 메세지입니다. 2'
                  },
                  onConfirm: () => { console.log('CONFIRM'); },
                  onCancel: () => { console.log('CANCEL 52374'); }
                });
              }}>
                PUSH MODAL
              </button>
            </div>
            <div>
              {JSON.stringify(state, '', 2)}
            </div>
          </div>
        )}
      </AppState.Consumer>
    </div>
  );
}

export default VMain;